.Preloader_ldsEllipsis__3SZJW {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}
.Preloader_ldsEllipsis__3SZJW div {
    position: absolute;
    top: 33px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: black;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.Preloader_ldsEllipsis__3SZJW div:nth-child(1) {
    left: 8px;
    animation: Preloader_lds-ellipsis1__1EWwZ 0.6s infinite;
}
.Preloader_ldsEllipsis__3SZJW div:nth-child(2) {
    left: 8px;
    animation: Preloader_lds-ellipsis2__2Mp94 0.6s infinite;
}
.Preloader_ldsEllipsis__3SZJW div:nth-child(3) {
    left: 32px;
    animation: Preloader_lds-ellipsis2__2Mp94 0.6s infinite;
}
.Preloader_ldsEllipsis__3SZJW div:nth-child(4) {
    left: 56px;
    animation: Preloader_lds-ellipsis3__1RPGU 0.6s infinite;
}
@keyframes Preloader_lds-ellipsis1__1EWwZ {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}
@keyframes Preloader_lds-ellipsis3__1RPGU {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}
@keyframes Preloader_lds-ellipsis2__2Mp94 {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(24px, 0);
    }
}

